<template>
  <div id="information">
    <h2>{{ product.title }}</h2>

    <p>
      <b>Productor:</b>
      {{ product.manufacturer }}
    </p>

    <p>
      <b>Reseñas:&nbsp;</b>
      {{ product.details.ratingsAverage }}
      <img src="@/assets/icons/star.png" alt="Rate" />
      ( {{ product.details.ratings.length }} Reseñas )
    </p>

    <p class="priceOffer">{{ product.price.toFixed(2) | priceFormat }}</p>

    <p v-if="product.offer > 0">
      <span>{{ price | priceFormat }}&nbsp;</span>
      <span class="offer">- {{ product.offer }}%</span>
    </p>

    <p>
      <b>Garantía:</b>
      {{ product.guaranty }} días
    </p>

    <p>
      <b>Tiempo de entrega:</b>
      {{ product.deliveryTime }} días
    </p>

    <p>
      <b>Disponibles:</b>
      {{ product.stock }}
    </p>

    <div>
      <select name="quantity" id="quantity">
        <option v-for="item in product.stock" :key="item" :value="item">{{ item }}</option>
      </select>
      <button id="addToCart">Añadir al carrito</button>
    </div>
  </div>
</template>

<script>
import urlBaseApi from "@/modules/urlBase";

export default {
  name: "InfoDetails",
  props: { product: { type: Object, required: true } },
  data() {
    return {};
  },
  mounted() {},
  computed: {
    price() {
      let x = (this.product.price / (1 - this.product.offer / 100)).toFixed(2);
      return x;
    },
    urlBaseApi() {
      return urlBaseApi + "img/";
    }
  },
  methods: {}
};
</script>

<style>
/* ****************** Information ****************/
#contentDetails #information {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 280px;
  height: 350px;
  overflow: hidden;
  padding: 10px;
  margin: 0 auto;
}
#contentDetails #information p {
  text-align: left;
  margin: 0;
  line-height: 2rem;
}
#contentDetails #information .priceOffer {
  font-size: 1.4rem;
  color: red;
  font-weight: bold;
}
#contentDetails #information p span {
  font-weight: lighter;
  text-decoration: line-through;
  color: rgb(170, 170, 170);
  font-size: 1rem;
  padding: 0px 5px;
}
#contentDetails #information .offer {
  text-decoration: none;
  color: white;
  background-color: green;
  border-radius: 5px;
}
#contentDetails #information img {
  height: 1.25rem;
}
#contentDetails #information select {
  width: 50px;
  height: 32px;
  padding: 5px;
  outline: none;
  border-radius: 5px;
  text-align: center;
}
#contentDetails #information #addToCart {
  font-size: 0.8rem;
  margin-left: 15px;
  padding: 10px;
  text-decoration: none;
  color: white;
  text-align: center;
  text-transform: uppercase;
  font-weight: 900;
  background-color: rgb(147, 226, 29);
  border-radius: 10px;
  border: 2px solid green;
}
#contentDetails #information #addToCart:hover {
  box-shadow: 0px 0px 5px gray;
  transform: scale(1.05, 1.05);
  background-color: green;
  color: white;
  text-decoration: none;
  cursor: pointer;
}
#contentDetails #information > div {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

/* ****************** Responsive *****************/
@media screen and (max-width: 740px) {
  #contentDetails #information {
    margin-top: 20px;
    grid-column: 1/3;
  }
}
@media screen and (max-width: 550px) {
  #contentDetails #information {
    width: 230px;
  }
}
</style>
