<template>
  <div id="mediaDetails">
    <div id="barImgs">
      <img
        v-for="(i,index) in product.img"
        :key="index"
        :src="urlBaseApi+i"
        :alt="product.title"
        :title="product.title"
        :class="imgActive[index]"
        @click="changeIndex(index)"
      />
    </div>

    <div id="imgProduct">
      <div id="imgSelected">
        <img :src="urlBaseApi+product.img[imgIndex]" :alt="'Imagen de '+  product.title " />
      </div>

      <div id="directions">
        <button id="btnPrev" @click.prevent="goToImage(-1)" title="Anterior">&lt;</button>
        <button id="btnFullScreen" @click.prevent="openFullscreen()"></button>
        <button id="btnNext" @click.prevent="goToImage(1)" title="Siguiente">&gt;</button>
      </div>
    </div>
  </div>
</template>

<script>
import urlBaseApi from "@/modules/urlBase";

export default {
  name: "MediaDetails",
  props: { product: { type: Object, required: true } },
  data() {
    return {
      error: true,
      imgActive: ["imgActive", "", ""],
      imgIndex: 0
    };
  },
  mounted() {
    this.getProduct();
  },
  computed: {
    urlBaseApi() {
      return urlBaseApi + "img/";
    }
  },
  methods: {
    getProduct() {
      this.imgActive[0] = "imgActive";
      this.imgActive[1] = "";
      this.imgActive[2] = "";
    },
    changeIndex(index) {
      this.imgIndex = index;

      this.imgActive.forEach((element, index) => {
        element = "";
        this.imgActive[index] = element;
      });

      this.imgActive[index] = "imgActive";
    },
    goToImage(n) {
      if (
        (this.imgIndex === 0 && n == -1) ||
        (this.imgIndex === this.product.img.length - 1 && n === 1)
      ) {
        this.changeIndex(this.imgIndex);
      } else {
        this.changeIndex((this.imgIndex += n));
      }
    },
    openFullscreen() {
      const imgSelected = document.getElementById("imgProduct");

      if (imgSelected.requestFullscreen) {
        imgSelected.requestFullscreen();
      } else if (imgSelected.mozRequestFullScreen) {
        /* Firefox */
        imgSelected.mozRequestFullScreen();
      } else if (imgSelected.webkitRequestFullscreen) {
        /* Chrome, Safari and Opera */
        imgSelected.webkitRequestFullscreen();
      } else if (imgSelected.msRequestFullscreen) {
        /* IE/Edge */
        imgSelected.msRequestFullscreen();
      }
    }
  },
  watch: {
    product: "getProduct"
  }
};
</script>

<style>
#mediaDetails {
  display: grid;
  grid-template-columns: auto auto;
}
/* ****************** Product Detail ****************/
#mediaDetails #imgProduct {
  grid-column: 2/3;
  width: 550px;
  height: 300px;
  overflow: hidden;
  padding: 20px;
  margin: auto;
  object-fit: contain;
}
#mediaDetails #imgProduct #imgSelected {
  height: 100%;
  width: 100%;
  margin: auto;
}
#mediaDetails #imgProduct #imgSelected img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  vertical-align: bottom;
  transition: all 3s;
}
/* Chrome, Safari and Opera syntax */
#mediaDetails #imgProduct:-webkit-full-screen #imgSelected {
  width: 100%;
  background-color: white;
}
/* Firefox syntax */
#mediaDetails #imgProduct:-moz-full-screen #imgSelected {
  width: 100%;
  background-color: white;
}
/* IE/Edge syntax */
#mediaDetails #imgProduct:-ms-fullscreen #imgSelected {
  width: 100%;
  background-color: white;
}
/* Standard syntax */
#mediaDetails #imgProduct:fullscreen #imgSelected {
  width: 100%;
  background-color: white;
}

/* **************** Control de imagenes ***************/
#mediaDetails #imgProduct #directions {
  width: 100%;
  height: 100%;
  position: relative;
  top: -100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#mediaDetails #imgProduct #directions button {
  display: flex;
  height: 100%;
  background-color: transparent;
  border: none;
}
#mediaDetails #imgProduct #directions #btnPrev,
#mediaDetails #imgProduct #directions #btnNext {
  justify-content: center;
  align-items: center;
  width: 15%;
  color: gainsboro;
  font-size: 7rem;
  cursor: pointer;
}
#mediaDetails #imgProduct #directions #btnFullScreen {
  width: 70%;
  cursor: zoom-in;
}
#mediaDetails #imgProduct #directions #btnPrev:hover,
#mediaDetails #imgProduct #directions #btnNext:hover {
  color: gray;
}

/* Chrome, Safari and Opera syntax */
#mediaDetails #imgProduct:-webkit-full-screen #directions #btnPrev,
#mediaDetails #imgProduct:-webkit-full-screen #directions #btnNext {
  width: 10%;
}
#mediaDetails #imgProduct:-webkit-full-screen #directions #btnFullScreen {
  width: 80%;
  cursor: auto;
}
/* Firefox syntax */
#mediaDetails #imgProduct:-moz-full-screen #directions #btnPrev,
#mediaDetails #imgProduct:-moz-full-screen #directions #btnNext {
  width: 10%;
}
#mediaDetails #imgProduct:-moz-full-screen #directions #btnFullScreen {
  width: 80%;
  cursor: auto;
}
/* IE/Edge syntax */
#mediaDetails #imgProduct:-ms-fullscreen #directions #btnPrev,
#mediaDetails #imgProduct:-ms-fullscreen #directions #btnNext {
  width: 10%;
}
#mediaDetails #imgProduct:-ms-fullscreen #directions #btnFullScreen {
  width: 80%;
  cursor: auto;
}
/* Standard syntax */
#mediaDetails #imgProduct:fullscreen #directions #btnPrev,
#mediaDetails #imgProduct:fullscreen #directions #btnNext {
  width: 10%;
}
#mediaDetails #imgProduct:fullscreen #directions #btnFullScreen {
  width: 80%;
  cursor: auto;
}

/* **************** Barra de imagenes **************/
#mediaDetails #barImgs {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
  height: 400px;
  padding: 20px;
  background-color: rgb(150, 150, 150);
  border-radius: 0 0 20px 0;
  box-shadow: 2px 2px 4px #555555 inset, 2px -2px 4px #555555 inset,
    2px 2px 4px gray;
}
#mediaDetails #barImgs img {
  height: calc((100% - 60px) / 3);
  background-color: white;
  width: auto;
  margin: 10px;
  object-fit: contain;
  cursor: pointer;
  opacity: 0.8;
  transition: all 300ms;
  border: 1px dashed yellow;
}
#mediaDetails #barImgs .imgActive {
  opacity: 1;
  transform: scale(1.1, 1.1);
  border: 3px solid black;
}
/* ****************** Responsive *****************/
@media screen and (max-width: 1200px) {
  #mediaDetails #imgProduct #directions #btnPrev,
  #mediaDetails #imgProduct #directions #btnNext {
    font-size: 6rem;
  }
}
@media screen and (max-width: 1000px) {
  #mediaDetails #imgProduct {
    height: 250px;
    width: auto;
  }
  #mediaDetails #barImgs {
    width: 80%;
    height: 350px;
  }
}
@media screen and (max-width: 770px) {
  #mediaDetails #imgProduct {
    height: 250px;
    width: auto;
  }
  #mediaDetails #imgProduct #directions #btnPrev,
  #mediaDetails #imgProduct #directions #btnNext {
    font-size: 5rem;
  }
  #mediaDetails #barImgs {
    width: 100%;
  }
}
@media screen and (max-width: 500px) {
  #mediaDetails #barImgs {
    height: 250px;
  }
}
@media screen and (max-width: 400px) {
  #mediaDetails #imgProduct #directions #btnPrev,
  #mediaDetails #imgProduct #directions #btnNext {
    font-size: 3rem;
  }
}
</style>
